body {
  text-align: center;
  width: 100vw;
  background-color: rgba(123,159, 53,1);
  font-family: Trebuchet MS,Lucida Grande,Lucida Sans Unicode,Lucida Sans,Tahoma,sans-serif; 
  color: rgba(  0, 51, 51,1);
}

button {
  background-color: rgba(123,159, 53,1);
  border: none;
  color: white;
  outline: none;
}

button:hover {
  color: rgba(170, 57, 57,1);
}

h2 {
  color: white;
  margin: auto;
}

#pomodoro {
  padding-top: 2%;
  width: 450px;
  margin: auto;
  display: grid;
  grid-template-areas:
      "title title"
      "session break"
      "timer timer";
}

#pomodoro-heading { grid-area: title; }
#session-container { grid-area: session; }
#break-container { grid-area: break; }
#timer-container { grid-area: timer; }

.container {
  margin: auto;
  margin-top: 5%;
  display: grid;
  padding: 5%;
  border: thick solid rgba(  0, 51, 51,1);
  border-radius: 10%;
}

#session-container, #break-container {
  grid-template-areas:
      "label label label"
      "decrement length increment";
}

#session-label, #break-label { grid-area: label; }
#session-length, #break-length { grid-area: length; }
#session-decrement, #break-decrement { grid-area: decrement; }
#session-increment, #break-increment { grid-area: increment; }

.length {
  font-size: 2.5em;
  margin: auto;
}

#pomodoro-heading {
  margin: auto;
  font-size: 3.5em;
  color: rgba(  0, 51, 51,1);
}

@font-face {
  font-family: 'digital-7-mono';
  src: url(/Pomodoro-Clock/static/media/digital-7.mono.58045dab.ttf);
}

#time-left {
  font-family: 'digital-7-mono', monospace;
  font-size: 8em;
}

#timer-label {
  font-size: 3em;
  margin-bottom: 5%;
}

#button-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

#timer-container {
  width: 60%;
  padding-right: 40px;
}
